:root {
  --blue-color: #00acdc;
}

html, body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  font-family: Roboto, serif;
  font-weight: 400;
}

header {
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
}

nav {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
  position: relative;
}

.img-wraper {
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

nav ul {
  justify-content: flex-end;
  width: 100%;
  display: flex;
  position: relative;
}

nav ul li {
  text-transform: uppercase;
  padding-bottom: 5px;
  list-style: none;
  display: inline-block;
  position: relative;
}

ul > li:nth-child(n+2) {
  margin-left: 20px;
}

li.is-active a:after {
  content: "";
  background-color: #00acdc;
  border-radius: 3px;
  width: 100%;
  height: 4px;
  transition: width .3s;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

li a {
  color: #000;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
}

a:hover {
  color: var(--blue-color);
}

.bottom-bar {
  background-color: var(--blue-color);
  border-radius: 3px;
  width: 0;
  height: 4px;
  transition: width .3s;
  position: absolute;
  bottom: -4px;
  left: 0;
}

li.is-active .bottom-bar {
  width: 100%;
}
